@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
/* FONTS */
@font-face {
    font-family: "Open Sans";
    src: local("Open Sans"),
        url(/portfolio/static/media/OpenSans-Regular.d7d7b835.ttf) format("truetype");
}

@font-face {
    font-family: "Baloo Tamma";
    src: local("Baloo Tamma"),
        url(/portfolio/static/media/BalooTamma-Regular.fc82b211.ttf) format("truetype");
}

@font-face {
    font-family: "Poppins", sans-serif;
    src: local("Poppins", sans-serif),
        url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
}

