/* CSS VARIABLES */
:root {
    --mainColor: #264653;
    --mainBGColor: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
    --logoColor: #dcfc8f;
}

/* GENERAL RULES */
button:focus {
    outline: 0 !important;
}

body {
    background-color: #f8f9fa;
}

.custom-divider {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.custom-divider-line:first-child {
    margin-right: 0.2rem;
}
.custom-divider-line:last-child {
    margin-left: 0.2rem;
}
.custom-divider-line {
    width: 100%;
    max-width: 4rem;
    height: 0.1rem;
    background-color: #264653;
    background-color: var(--mainColor);
    border-radius: 1rem;
}
.fas.fa-s::before {
    color: #264653;
    color: var(--mainColor);
}

a.btn-outline-dark {
    color: #264653;
    color: var(--mainColor);
    border-color: #264653;
    border-color: var(--mainColor);
}

a.btn-outline-dark:hover {
    background-color: #264653;
    background-color: var(--mainColor);
    color: #fff;
}

.custom-navbar {
    background-color: var(--mainColor);
}
.custom-navbar a.navbar-brand {
    font-size: 1.5rem;
}

span.logo {
    font-family: "Permanent Marker", cursive;
    color: var(--logoColor);
}

.custom-navbar .navbar-nav .nav-link.active,
.custom-navbar .navbar-nav .nav-link {
    font-size: 1.2rem;
    transition: color 0.3s ease-in-out;
    color: #f8f8f8;
    transition: all 0.5s ease;
}

.home {
    height: calc(100% - 94px);
    min-height: 500px;
    background-color: #ffe156;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
}

.home-content img.img-fluid {
    border-radius: 50%;
    border: 5px solid var(--mainColor);
    background-color: #f8f8f8;
}

.home-content-text span.greeting {
    color: var(--mainColor);
    margin-top: 15px;
    font-weight: normal;
    display: block;
    letter-spacing: 5px;
    text-align: left;
    font-size: 2.5rem;
    text-transform: capitalize;
    animation: myAnim 1s ease 1s 1 normal forwards;
}

.home-content-text span.name {
    display: block;
    margin-top: 0;
    font-size: 3.5rem;
    font-weight: bold;
    letter-spacing: 6px;
    text-transform: capitalize;
    color: #dc3545;
    font-family: "Poppins", sans-serif;
}

.home-content-text span.job {
    color: var(--mainColor);
    letter-spacing: 5px;
    font-size: 3rem;
    text-transform: capitalize;
    margin-bottom: 40px;
    display: block;
}

.home-content-text p.lead {
    color: var(--mainColor);
}

.effect-hover {
    overflow: hidden;
}

.effect-hover img {
    transform: scale(0.8);
    transition: all 0.5s ease;
}

.effect-hover img:hover {
    transform: scale(1) rotate(360deg);
}

/* RESPONSIVE */
@media (max-width: 575px) {
    .home {
        min-height: 400px;
    }
    .home-content-text span.greeting {
        text-align: center;
        font-size: 1.5rem;
    }
    .home-content-text span.name {
        text-align: center;
        font-size: 1.5rem;
        letter-spacing: 4px;
    }

    .home-content-text span.job {
        text-align: center;
        font-size: 0.75rem;
    }

    .home-content-text button.btn-outline-dark {
        width: 100%;
        display: block;
    }
}

.projects h2 {
    margin-bottom: 30px;
    font-size: 2rem;
    color: var(--mainColor);
}

.projects .proj {
    padding: 0;
    position: relative;
}

.text .data {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 85%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #000;
    font-size: 15px;
    font-family: "Baloo Tamma", cursive;
    border-left: 5px solid var(--mainColor);
    padding: 15px 10px;
}

.text .data h5 {
    color: #dc3545;
}

.text .data h6 {
    color: var(--mainColor);
}

.text a {
    color: var(--mainColor);
    position: absolute;
    bottom: 5%;
    right: 5%;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    font-family: "Open Sans", sans-serif;
    cursor: pointer;
}

/* RESPONSIVE */
@media (max-width: 575px) {
    .projects h2 {
        font-size: 1.5rem;
    }
}

/* Large devices (desktops, 992px and up) = lg */
@media (min-width: 992px) {
    .projects .proj .overlay {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        background-image: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
        overflow: hidden;
        width: 100%;
        height: 100%;
        transition: 0.3s ease-in-out;
        transform: scale(0);
        opacity: 0.8;
    }

    .projects .proj:hover .overlay {
        transform: scale(1);
        opacity: 1;
    }
}

.about-section {
    background-color: #1abc9c;
    min-height: 300px;
}

.about-section h2 {
    color: #fff;
    font-weight: bold;
}

.about-content p.lead {
    font-size: 1.5rem;
    font-family: "Open Sans";
}

.about-section .custom-divider .custom-divider-icon .fas.fa-star {
    color: var(--mainColor);
}

/* RESPONSIVE */
@media (max-width: 575px) {
    .about-section {
        padding: 15px 0;
    }
    .about-section h2 {
        font-size: 1.5rem;
    }

    .about-content p.lead {
        text-align: justify;
    }
}

.contact {
    background-color: #2c3e50;
    background-size: cover;
    background-attachment: fixed;
}

.contact h2 {
    color: #fff;
}

.contact .custom-divider .custom-divider-icon .fas.fa-star {
    color: #1abc9c;
}
.contact .custom-divider .custom-divider-line {
    background-color: #1abc9c;
}

.btn-outline-light.btn-social:hover {
    background-color: #1abc9c;
    border-color: #1abc9c;
}

/* RESPONSIVE */
@media (max-width: 575px) {
    .contact h2 {
        font-size: 1.5rem;
    }
}

footer {
    background-color: #1a252f;
}

